@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body{
    background-image: -webkit-gradient(linear, left top, right top, from(#575fcf), to(#3498db));
    background-image: linear-gradient(to right, #575fcf, #3498db);
    color:white;
}

body, input, button {
    font-size:1em;
    font-family: 'Open Sans', sans-serif;
}
header{
    width: 100%;
    background-color:#FFF;
    text-align: center;
    padding: 45px 0px;
    color:rgba(41, 128, 185,1.0);
}
header h1{
    font-weight: bold;
    font-size:1.7em;
    text-transform: uppercase;
}
header h6{
    font-style: italic;
    font-weight: 100;
    font-size:1em;
}
::-webkit-input-placeholder{
    text-indent: 15px;
    color:#333;
}
::-moz-placeholder{
    text-indent: 15px;
    color:#333;
}
:-ms-input-placeholder{
    text-indent: 15px;
    color:#333;
}
::-ms-input-placeholder{
    text-indent: 15px;
    color:#333;
}
::placeholder{
    text-indent: 15px;
    color:#333;
}
.clear{
    clear: both;
}
.App{
    padding: 30px 0px;
    width:80%;
    margin:0 auto;
}
.btn_mais{
    border:0;
    width:100%;
    background-color: #ff5e57;
    color:#FFF;
    margin: 15px auto;
    padding:15px;
    font-size:1.1em;
    -webkit-transition: all .8s;
    transition: all .8s;
    font-weight: bold;
}
.btn_mais:hover{
    background-color:#ff3f34;
}

footer{
    text-align: center;
    position: fixed;
    bottom:0;
    width:100%;
    background-color:#FFF;
    color:#000;
    padding:15px 0px;
}
footer p{
    font-size:1.2em;
}
footer span{
    font-size:.8em;
}
footer a {
    color:#ff5e57;
    text-decoration: none;
    font-weight: bold;
}
footer a:hover{
    text-decoration: underline;
}

@media (max-width: 640px){
    .App{
        width:100%;
    }
    .login{
        width:90%;
        margin: 0 auto;
    }
}

.login{
    margin: 30px auto;
    width:80%;
}
.login input{
    text-indent: 15px;
    border:0;
    background-color: rgba(236, 240, 241,.8);
    color:#333;
    font-size:1.1em;
    width:100%;
    margin: 15px auto;
    padding: 15px 0px;
}

.btn{
    border:0;
    width:80%;
    background-color: rgba(236, 240, 241,.8);
    color:#333;
    margin: 15px auto;
    padding:15px;
    font-size:1.1em;
    -webkit-transition: all .8s;
    transition: all .8s;
    font-weight: bold;
}
.btn:hover{
    background-color: rgba(236, 240, 241,1);
    color:#3498db;
}
.text-center{
    text-align: center;
}
.duvidas{
    background-color:rgba(255,255,255,.8);
    color:#000;
    width: 100%;
    padding: 15px 0px;
}
.list{
    width:70%;
    margin:5px auto;
}
.list h2{
    line-height: 25px;
    color:black;
    font-weight: bold;
    font-size:1.5em;
    text-indent: 25px;
}
.list h3{
    
    line-height: 25px;
}
.list strong{
    font-weight: bold;
}
.col-8{
    width:80%;
    margin:0 auto;}
